<template>
    <div class="relatedPartyMatch">
        <a-spin tip="加载中..." :spinning="spinning" size="large">
            <div style="text-align: center; margin-bottom: 8px">
                <a-icon type="tag" style="margin-right: 8px" />{{
                    `共查询到${pagination1.total || 0}条数据 `
                }}
                <span
                    @click="manualRefesh"
                    style="color: #409eff; margin-left: 10px; cursor: pointer"
                >
                    <a-icon type="sync" style="margin-right: 5px" />刷新
                </span>
                &nbsp;&nbsp;&nbsp;&nbsp;{{ `已匹配${relatioNum}个关联方` }}
            </div>
            <a-table 
                class="related-parrty-table"
                bordered
                :columns="columns"
                :data-source="dataCollection"
                :scroll="{ x: 'calc(1300px + 50%)', y: 'calc(100% - 55px)' }"
                :pagination="pagination1"
                :components='components'
            >
                <template slot="autoMatch" slot-scope="text, record" >
                    <!-- <a-button
                        type="primary"
                        shape="circle"
                        @click="handleMatch(record)"
                        icon="link"
                        size="small"
                        title="手动匹配"
                        class="match-btn"
                    /> -->
                    <a-popconfirm
                        title="确定要取消匹配的数据吗？"
                        ok-text="确 定"
                        cancel-text="取 消"
                        @confirm="deleteMatch(record)"
                    >
                        <a-button
                            type="primary"
                            shape="circle"
                            icon="disconnect"
                            size="small"
                            title="取消匹配"
                            class="del-match-btn"
                        />
                    </a-popconfirm>
                    <a-button
                        type="primary"
                        shape="circle"
                        icon="eye"
                        @click="handleCheckAccountAge(record)"
                        size="small"
                        title="查看账龄"
                    />
                    <a-popconfirm
                        title="确定要删除匹配的数据吗？"
                        ok-text="确 定"
                        cancel-text="取 消"
                        @confirm="deleteMerchantRelatedById(record)"
                    >
                        <a-button
                            type="primary"
                            shape="circle"
                            icon="delete"
                            size="small"
                            title="删除"
                            class="del-match-btn"
                        />
                    </a-popconfirm>
                </template>
            </a-table>
            <a-modal
                v-model="openSearchDiaplog"
                title="查询"
                ok-text="确"
                cancel-text="取消"
                @ok="onSubmitSeachItem(null)"
                @cancel="cancelSearch"
            >
                <a-form-model
                    :model="searchForm"
                    :label-col="labelCol"
                    :wrapper-col="wrapperCol"
                >
                    <a-form-model-item label="关联方名称">
                        <a-input
                            v-model="searchForm.mergeCustomerName"
                            placeholder="请输入"
                        />
                    </a-form-model-item>
                    <a-form-model-item label="客商名称">
                        <a-input
                            v-model="searchForm.merchantName"
                            placeholder="请输入"
                        />
                    </a-form-model-item>
                    <a-form-model-item label="统一信用代码">
                        <a-input
                            v-model="searchForm.customerUscc"
                            placeholder="请输入"
                        />
                    </a-form-model-item>
                    <a-form-model-item label="匹配">
                        <a-radio-group v-model="matchValue">
                            <a-radio :value="1"> 是 </a-radio>
                            <a-radio :value="0"> 否 </a-radio>
                        </a-radio-group>
                    </a-form-model-item>
                </a-form-model>
            </a-modal>
            <a-modal
                v-model="openHandMatchDiaplog"
                class="matchDialog"
                title="匹配列表"
                ok-text="确认"
                cancel-text="取消"
                @ok="onSubmitMatchItem"
                @cancel="cancelMatch"
                width="90%"
            >
                <a-table
                    bordered
                    :columns="columns"
                    :data-source="matchTabledataCollection"
                    :row-selection="{
                        selectedRowKeys: selectedRowKeys,
                        onChange: matchTableOnSelectChange,
                    }"
                    :scroll="{ y: 'calc(100% - 55px)' }"
                    :pagination="pagination"
                >
                    <div
                        slot="filterDropdown"
                        slot-scope="{
                            setSelectedKeys,
                            selectedKeys,
                            confirm,
                            clearFilters,
                            column,
                        }"
                        style="padding: 8px"
                    >
                        <a-input
                            v-ant-ref="(c) => (searchInput = c)"
                            placeholder="请输入"
                            :value="selectedKeys[0]"
                            style="
                                width: 188px;
                                margin-bottom: 8px;
                                display: block;
                            "
                            @change="
                                (e) =>
                                    setSelectedKeys(
                                        e.target.value ? [e.target.value] : []
                                    )
                            "
                            @pressEnter="
                                () =>
                                    handleSearch(
                                        selectedKeys,
                                        confirm,
                                        column.dataIndex
                                    )
                            "
                        />
                        <a-button
                            type="primary"
                            icon="search"
                            size="small"
                            style="width: 90px; margin-right: 8px"
                            @click="
                                handleSearch(
                                    selectedKeys,
                                    confirm,
                                    column.dataIndex
                                )
                            "
                        >
                            搜 索
                        </a-button>
                        <a-button
                            size="small"
                            style="width: 90px"
                            @click="handleReset(column.dataIndex, clearFilters)"
                        >
                            重 置
                        </a-button>
                    </div>
                </a-table>
            </a-modal>
            <a-modal
                v-model="visiableUploadDialog"
                title="关联方导入"
                on-ok="handleConfirmUpload"
                class="upload-dialog"
            >
                <template slot="footer">
                    <a-button key="back" @click="handleCancelUpload">
                        取 消
                    </a-button>
                    <a-button
                        key="submit"
                        type="primary"
                        @click="handleConfirmUpload"
                    >
                        确 定
                    </a-button>
                </template>
                <a-input v-model="fileName" class="file-name" />
                <a-upload
                    :before-upload="beforeUpload"
                    name="file"
                    accept="'xls', 'xlsx'"
                    listType="text"
                >
                    <a-button type="primary">
                        <a-icon type="upload" /> 浏 览
                    </a-button>
                </a-upload>
                <a-button
                    type="primary"
                    icon="download"
                    @click="downLoadImportFile"
                    >模板文件下载</a-button
                >
            </a-modal>
            <a-modal
                v-model="visiableRelateListDialog"
                width="80%"
                :footer="null"
                :destroyOnClose="true"
                class="relate-list-dialog matchDialog"
            >
                <template slot="title">
                    <div class="headerTitle">
                        <span class="a-dialog__title">关联方清单</span>
                        <a-button
                            icon="retweet"
                            title="同步"
                            @click="handleSyncUploadCustomer"
                        />
                    </div>
                </template>
                <a-table
                    :pagination="relateListPagination"
                    :columns="relateListColumns"
                    :data-source="relateList"
                    :scroll="{ y: 'calc(100% - 55px)' }"
                />
            </a-modal>
            <a-modal
                v-model="openAccountAgeDialog"
                title="查看账龄列表"
                width="90%"
                :footer="null"
                class="accountAgeDialog matchDialog"
                :destroyOnClose="true"
            >
                <a-table
                    bordered
                    :columns="accountAgeTableColumns"
                    :data-source="accountAgeTabledataCollection"
                    :scroll="{ y: 'calc(100% - 55px)', x: 'scollXWidth' }"
                    :pagination="pagination2"
                >
                    <div
                        slot="filterDropdown"
                        slot-scope="{
                            setSelectedKeys,
                            selectedKeys,
                            confirm,
                            clearFilters,
                            column,
                        }"
                        style="padding: 8px"
                    >
                        <a-input
                            v-ant-ref="(c) => (searchInput = c)"
                            placeholder="请输入"
                            :value="selectedKeys[0]"
                            style="
                                width: 188px;
                                margin-bottom: 8px;
                                display: block;
                            "
                            @change="
                                (e) =>
                                    setSelectedKeys(
                                        e.target.value ? [e.target.value] : []
                                    )
                            "
                            @pressEnter="
                                () =>
                                    handleSearchDebtor(
                                        selectedKeys,
                                        confirm,
                                        column.dataIndex
                                    )
                            "
                        />
                        <a-button
                            type="primary"
                            icon="search"
                            size="small"
                            style="width: 90px; margin-right: 8px"
                            @click="
                                handleSearchDebtor(
                                    selectedKeys,
                                    confirm,
                                    column.dataIndex
                                )
                            "
                        >
                            搜 索
                        </a-button>
                        <a-button
                            size="small"
                            style="width: 90px"
                            @click="handleResetDebtor(clearFilters)"
                        >
                            重 置
                        </a-button>
                    </div>
                </a-table>
            </a-modal>
        </a-spin>
    </div>
</template>
<script>
import XEUtils from 'xe-utils';
import { formatter } from '../libs/utils/formatter';
import session from '@data/session/sessionUtils.js';

export default {
    name: 'arrival',
    computed: {
        rowSelection() {
            return {
                onChange: (selectedRowKeys, selectedRows) => {},
            };
        },
    },
    name: 'relatedPartyMatch',
    computed: {
        refresh() {
            return this.$store.state.refreshRelatedPartMatch;
        },
        visibleuPloadDialog() {
            return this.$store.state.uploadDialog;
        },
    },
    watch: {
        refresh: {
            handler(val) {
                if (val) this.handleQueryContactsCustomer();
            },
            immediate: true,
        },
    },
    data() {
        this.components = {
            header: {
                cell: (h, props, children) => {
                    const { key, ...restProps } = props;
                    // console.log('ResizeableTitle', key);
                    const col = this.columns.find((col) => {
                        const k = col.dataIndex || col.key;
                        return k === key;
                    });

                    if (!col || !col.width) {
                        return h('th', { ...restProps }, [...children]);
                    }

                    const dragProps = {
                        key: col.dataIndex || col.key,
                        class: 'table-draggable-handle',
                        attrs: {
                            w: 10,
                            x: col.width,
                            z: 1,
                            axis: 'x',
                            draggable: true,
                            resizable: false,
                        },
                        on: {
                            dragging: (x, y) => {
                                col.width = Math.max(x, 1);
                            },
                        },
                    };
                    const drag = h('vue-draggable-resizable', { ...dragProps });
                    return h('th', { ...restProps, class: 'resize-table-th' }, [
                        ...children,
                        drag,
                    ]);
                },
            },
        };
        return {
            matchValue: null,
            currentRow: {},
            spinning: false,
            mouseLeaveDelay: 0.05,
            columns: [
                {
                    title: '序号',
                    dataIndex: 'index',
                    key: 'index',
                    align: 'center',
                    width: 50,
                    customRender: (text, record, index) => `${index + 1}`,
                },
                {
                    title: '关联方名称',
                    dataIndex: 'mergeCustomerName',
                    ellipsis: true,
                    width: 300,
                    customRender: (value, row, index) => {
                        const obj = {
                            children: value,
                            attrs: {},
                        };
                        const arrResult = formatter.mergeCells(
                            this.dataCollection,
                            'mergeCustomerName'
                        );
                        for (let i = 0; i < arrResult[0].length; i++) {
                            if (index === arrResult[1][i]) {
                                obj.attrs.rowSpan = arrResult[0][i];
                            }
                            if (
                                index > arrResult[1][i] &&
                                index <= arrResult[1][i + 1]
                            ) {
                                obj.attrs.rowSpan = 0;
                            }
                        }
                        return obj;
                    },
                },
                {
                    title: '类型',
                    dataIndex: 'mergeScopeName',
                    width: 170,
                    scopedSlots: { customRender: 'mergeScopeName' },
                },
                {
                    title: '操作',
                    dataIndex: 'autoMatch',
                    width: 120,
                    scopedSlots: { customRender: 'autoMatch' },
                },
                {
                    title: '认缴比例',
                    dataIndex: 'stockBreakRate',
                    width: 90,
                    customRender: formatter.fmtEquity,
                },
                {
                    title: '权益比例',
                    width: 90,
                    dataIndex: 'equityRatio',
                    customRender: formatter.fmtEquity,
                },
                {
                    title: '持股方式',
                    width: 90,
                    dataIndex: 'stockType',
                },
                {
                    title: '认缴金额',
                    width: 150,
                    dataIndex: 'stockCapi',
                    align: 'right',
                    customRender: formatter.commafy,
                },
                {
                    title: '客商编号',
                    width: 150,
                    dataIndex: 'merchantId',
                    ellipsis: true,
                },
                {
                    title: '客商名称',
                    width: 200,
                    dataIndex: 'merchantName',
                    ellipsis: true,
                },
                {
                    title: '科目名称',
                    dataIndex: 'subjectName',
                    width: 200,
                    ellipsis: true,
                },
                {
                    title: '统一信用代码',
                    dataIndex: 'customerUscc',
                    width: 200,
                    ellipsis: true,
                },
                {
                    title: '工商注册名称',
                    width: 280,
                    dataIndex: 'customerName',
                    ellipsis: true,
                },
                {
                    title: '工商办公地址',
                    width: 260,
                    dataIndex: 'officeAddress',
                    ellipsis: true,
                },
                {
                    title: '工商注册地址',
                    width: 260,
                    dataIndex: 'registeredAddress',
                    ellipsis: true,
                },
                {
                    title: '百度地址',
                    width: 260,
                    dataIndex: 'baiduAddress',
                    ellipsis: true,
                },
            ],
            dataCollection: [],
            openSearchDiaplog: false,
            searchForm: {
                mergeCustomerName: null,
                merchantName: null,
                customerUscc: null,
            },
            labelCol: { span: 6 },
            wrapperCol: { span: 13 },
            openHandMatchDiaplog: false,
            matchTableColumns: [
                {
                    title: '统一信用代码',
                    dataIndex: 'customerUscc',
                    customRender: (value, row, index) => {
                        const obj = {
                            children: value,
                            attrs: {},
                        };
                        const arrResult = formatter.mergeCells(
                            this.matchTabledataCollection,
                            'customerUscc'
                        );
                        for (let i = 0; i < arrResult[0].length; i++) {
                            if (index === arrResult[1][i]) {
                                obj.attrs.rowSpan = arrResult[0][i];
                            }
                            if (
                                index > arrResult[1][i] &&
                                index <= arrResult[1][i + 1]
                            ) {
                                obj.attrs.rowSpan = 0;
                            }
                        }
                        return obj;
                    },
                    scopedSlots: {
                        filterDropdown: 'filterDropdown',
                        filterIcon: 'filterIcon',
                        customRender: 'customRender',
                    },
                },
                {
                    title: '工商名称',
                    dataIndex: 'customerName',
                    scopedSlots: {
                        filterDropdown: 'filterDropdown',
                        filterIcon: 'filterIcon',
                        customRender: 'searchCustomerName',
                    },
                },
                {
                    title: '客商代码',
                    dataIndex: 'merchantId',
                },
                {
                    title: '客商名称',
                    dataIndex: 'merchantName',
                    ellipsis: true,
                    scopedSlots: {
                        filterDropdown: 'filterDropdown',
                        filterIcon: 'filterIcon',
                        customRender: 'searchMerchantName',
                    },
                },
            ],
            matchTabledataCollection: [],
            selectedRowKeys: [],
            pagination: {
                current: 1,
                pageSize: 10,
                showSizeChanger: true,
                pageSizeOptions: ['10', '20', '30', '40', '50'],
                onShowSizeChange: (current, pageSize) => {
                    this.queryParams.sqlId = 'DG40003';
                    this.queryParams.start = current - 1;
                    this.queryParams.limit = pageSize;
                    this.queryParams.isChange = true;
                    this.pagination.pageSize = pageSize;
                    this.queryMerchantListData();
                },
                showQuickJumper: false,
                size: 'small',
                total: 0,
                showTotal: () => `共${this.pagination.total}条`,
                change: true,
                onChange: (current, size) => {
                    this.queryParams.sqlId = 'DG40003';
                    this.queryParams.start = (current - 1) * size;
                    this.queryParams.limit = this.pagination.pageSize;
                    this.queryParams.isChange = false;
                    this.pagination.current = current;
                    this.queryMerchantListData();
                },
            },
            pagination1: {
                current: 1,
                pageSize: 10,
                showSizeChanger: true,
                pageSizeOptions: ['10', '20', '30', '40', '50'],
                onShowSizeChange: (current, pageSize) => {
                    this.pagination1.pageSize = pageSize;
                    let obj = {
                        start: (current - 1) * pageSize,
                        limit: this.pagination1.pageSize,
                        param3: this.searchFormCache.mergeCustomerName || null,
                        param4: this.searchFormCache.merchantName || null,
                        param5: this.searchFormCache.customerUscc || null,
                        param6: this.searchFormCache.matchValue || null,
                    };
                    let flag =
                        XEUtils.get(
                            this.searchFormCache,
                            'mergeCustomerName'
                        ) ||
                        XEUtils.get(this.searchFormCache, 'merchantName') ||
                        XEUtils.get(this.searchFormCache, 'customerUscc') ||
                        XEUtils.get(this.searchFormCache, 'matchValue');
                    flag != null
                        ? this.onSubmitSeachItem(obj)
                        : this.handleQueryContactsCustomer({
                              start: (current - 1) * pageSize,
                              limit: this.pagination1.pageSize,
                          });
                },
                showQuickJumper: false,
                size: 'small',
                total: 0,
                showTotal: () => `共${this.pagination1.total}条`,
                change: true,
                onChange: (current, size) => {
                    this.pagination1.current = current;
                    let obj = {
                        start: (current - 1) * size,
                        limit: this.pagination1.pageSize,
                        param3: this.searchFormCache.mergeCustomerName || null,
                        param4: this.searchFormCache.merchantName || null,
                        param5: this.searchFormCache.customerUscc || null,
                        param6: this.searchFormCache.matchValue || null,
                    };
                    let flag =
                        XEUtils.get(
                            this.searchFormCache,
                            'mergeCustomerName'
                        ) ||
                        XEUtils.get(this.searchFormCache, 'merchantName') ||
                        XEUtils.get(this.searchFormCache, 'customerUscc') ||
                        XEUtils.get(this.searchFormCache, 'matchValue');
                    flag != null
                        ? this.onSubmitSeachItem(obj)
                        : this.handleQueryContactsCustomer({
                              start: (current - 1) * size,
                              limit: this.pagination1.pageSize,
                          });
                },
            },
            queryParams: {
                sqlId: 'DG40003',
                start: 0,
                limit: 10,
            },
            selectedMatchRows: [],
            uploading: false,
            visiableUploadDialog: false,
            fileName: null,
            uploadFile: null,
            searchText: '',
            searchInput: null,
            matchDialogSerch: false,
            searchedColumn: null,
            visiableRelateListDialog: false,
            relateList: [],
            relateListColumns: [
                {
                    title: '关联方名称',
                    dataIndex: 'mergeCustomerName',
                    ellipsis: true,
                    width: '200px',
                },
                {
                    title: '统一信用代码',
                    dataIndex: 'uscc',
                    ellipsis: true,
                    width: '200px',
                },
                {
                    title: '认缴比例',
                    dataIndex: 'stockBreakRate',
                    customRender: formatter.fmtEquity,
                    width: '200px',
                },
                {
                    title: '权益比例',
                    dataIndex: 'equityRatio',
                    customRender: formatter.fmtEquity,
                    width: 100,
                },
                {
                    title: '持股方式',
                    dataIndex: 'stockType',
                    width: 100,
                },
                {
                    title: '认缴金额',
                    dataIndex: 'stockCapi',
                    width: '120px',
                    align: 'right',
                    customRender: formatter.commafy,
                },
            ],
            accountAgeTableColumns: [
                {
                    title: '债务人编号',
                    dataIndex: 'araId',
                    ellipsis: true,
                    width: '120px',
                },
                {
                    title: '债务人名称',
                    dataIndex: 'araName',
                    ellipsis: true,
                    width: '120px',
                    scopedSlots: {
                        filterDropdown: 'filterDropdown',
                        filterIcon: 'filterIcon',
                        customRender: 'searchMerchantName',
                    },
                },
                {
                    title: '数据插入状态',
                    dataIndex: 'status',
                    ellipsis: true,
                    width: '120px',
                },
                {
                    title: '原因',
                    dataIndex: 'reason',
                    ellipsis: true,
                    width: '120px',
                },
                {
                    title: '账龄',
                    dataIndex: 'accountAge',
                    width: '550px',
                    children: [
                        {
                            title: '1年内',
                            width: '110px',
                            dataIndex: 'ara01',
                            align: 'right',
                            customRender: formatter.commafy,
                        },
                        {
                            title: '1-2年',
                            width: '100px',
                            dataIndex: 'ara02',
                            align: 'right',
                            customRender: formatter.commafy,
                        },
                        {
                            title: '2-3年',
                            width: '100px',
                            dataIndex: 'ara03',
                            align: 'right',
                            customRender: formatter.commafy,
                        },
                        {
                            title: '3-4年',
                            width: '110px',
                            dataIndex: 'ara04',
                            align: 'right',
                            customRender: formatter.commafy,
                        },
                        {
                            title: '4-5年',
                            width: '110px',
                            dataIndex: 'ara05',
                            align: 'right',
                            customRender: formatter.commafy,
                        },
                        {
                            title: '5年以上',
                            width: '110px',
                            dataIndex: 'ara06',
                            align: 'right',
                            customRender: formatter.commafy,
                        },
                    ],
                },
            ],
            accountAgeTabledataCollection: [],
            openAccountAgeDialog: false,
            curstomerInformation: {},
            pagination2: {
                current: 1,
                pageSize: 10,
                showSizeChanger: true,
                pageSizeOptions: ['10', '20', '30', '40', '50'],
                onShowSizeChange: (current, pageSize) => {
                    this.pagination2.pageSize = pageSize;
                    this.pagination2.current = 1;
                    let obj = {
                        sqlId: 'DG40006',
                        lockCustomerId:
                            this.checkAccountAgeOldItem.mergeCustomerId,
                        lockProjectId:
                            this.checkAccountAgeOldItem.mergeProjectId,
                        lockYyyy:
                            this.checkAccountAgeOldItem.mergeCurAccYear,
                        param3: null,
                        param4: null,
                        param5: 1,
                        start: 0,
                        limit: pageSize,
                    };
                    this.getAccountAgeDataAndRelatedpartListData(
                        obj,
                        'accountAge'
                    );
                },
                showQuickJumper: false,
                size: 'small',
                total: 0,
                showTotal: () => `共${this.pagination2.total}条`,
                change: true,
                onChange: (current, size) => {
                    this.pagination2.current = current;
                    let obj = {
                        sqlId: 'DG40006',
                        lockCustomerId:
                            this.checkAccountAgeOldItem.mergeCustomerId,
                        lockProjectId:
                            this.checkAccountAgeOldItem.mergeProjectId,
                        lockYyyy:
                            this.checkAccountAgeOldItem.mergeCurAccYear,
                        param3: null,
                        param4: null,
                        param5: 1,
                        start: (current - 1) * size,
                        limit: this.pagination2.pageSize,
                    };
                    this.getAccountAgeDataAndRelatedpartListData(
                        obj,
                        'accountAge'
                    );
                },
            },
            checkAccountAgeOldItem: {},
            relateListPagination: {
                current: 1,
                pageSize: 10,
                showSizeChanger: true,
                pageSizeOptions: ['10', '20', '30', '40', '50'],
                onShowSizeChange: (current, pageSize) => {
                    this.relateListPagination.pageSize = pageSize;
                    let obj = {
                        sqlId: 'DG40006',
                        lockCustomerId: this.curstomerInformation.curCustomerId,
                        lockProjectId: this.curstomerInformation.curProjectId,
                        lockYyyy: this.curstomerInformation.curAccYear,
                        start: current - 1,
                        limit: this.relateListPagination.pageSize,
                    };
                    this.getAccountAgeDataAndRelatedpartListData(obj);
                },
                showQuickJumper: false,
                size: 'small',
                total: 0,
                showTotal: () => `共${this.relateListPagination.total}条`,
                change: true,
                onChange: (current, size) => {
                    this.relateListPagination.current = current;
                    let obj = {
                        sqlId: 'DG40006',
                        lockCustomerId: this.curstomerInformation.curCustomerId,
                        lockProjectId: this.curstomerInformation.curProjectId,
                        lockYyyy: this.curstomerInformation.curAccYear,
                        start: (current - 1) * size,
                        limit: this.relateListPagination.pageSize,
                    };

                    this.getAccountAgeDataAndRelatedpartListData(obj);
                },
            },
            searchFormCache: {},
            relatioNum: 0,
        };
    },
    methods: {
        handleSyncUploadCustomer() {
            //同步
            let that = this;
            this.$confirm({
                title: '提示',
                content: '确认要同步关联方清单吗？',
                onOk() {
                    that.spinning = true;
                    that.$sacpApis
                        .synContacts()
                        .then((res) => {
                            if (!res.success)
                                return that.$message.error(
                                    res.resultInfo.statusText
                                );
                            that.openRelateListDialog(
                                that.curstomerInformation,
                                'sync'
                            );
                            // that.onSubmitSeachItem();
                            that.$message.success('同步成功');
                            // this.$message.success("同步成功")
                        })
                        .catch((err) => that.$message.error(err))
                        .finally(() => (that.spinning = false));
                },
                onCancel() {},
            });
        },
        handleSearch(selectedKeys, confirm, dataIndex) {
            this.searchText = selectedKeys[0];
            this.searchedColumn = dataIndex;
            if (this.searchedColumn && this.searchedColumn === 'merchantName') {
                this.queryParams.param3 = this.searchText;
                // this.queryParams.param5=null;
            } else if (
                this.searchedColumn &&
                this.searchedColumn === 'customerUscc'
            ) {
                // this.queryParams.param3=null;
                this.queryParams.param5 = this.searchText;
            } else if (
                this.searchedColumn &&
                this.searchedColumn === 'customerName'
            ) {
                this.queryParams.param6 = this.searchText;
            }
            this.queryMerchantListData();
        },
        handleSearchDebtor(selectedKeys, confirm, dataIndex) {
            //债务人名称过滤
            this.searchText = selectedKeys[0];
            let obj = {
                sqlId: 'DG40016',
                lockCustomerId: this.checkAccountAgeOldItem.mergeCustomerId,
                lockProjectId: this.checkAccountAgeOldItem.mergeProjectId,
                lockYyyy: this.checkAccountAgeOldItem.mergeCurAccYear,
                param3: selectedKeys[0],
                param4: null,
                param5: 0,
                start: this.queryParams.start,
                limit: this.queryParams.limit,
            };

            this.getAccountAgeDataAndRelatedpartListData(
                JSON.parse(JSON.stringify(obj)),
                'accountAge'
            );
        },
        handleResetDebtor(clearFilters) {
            clearFilters();
            this.searchText = '';
            let obj = {
                sqlId: 'DG40006',
                lockCustomerId: this.checkAccountAgeOldItem.mergeCustomerId,
                lockProjectId: this.checkAccountAgeOldItem.mergeProjectId,
                lockYyyy: this.checkAccountAgeOldItem.mergeCurAccYear,
                param3: null,
                param4: null,
                param5: 1,
                start: this.queryParams.start,
                limit: this.queryParams.limit,
            };
            this.getAccountAgeDataAndRelatedpartListData(
                JSON.parse(JSON.stringify(obj)),
                'accountAge'
            );
        },
        openRelateListDialog(value, str) {
            //合并项目获取关联方清单
            if (!value) return this.$message.error('请重新登录');
            this.curstomerInformation = value;
            this.visiableRelateListDialog = true;
            let obj = {
                sqlId: 'DG40006',
                lockCustomerId: value.curCustomerId,
                lockProjectId: value.curProjectId,
                lockYyyy:  value.curAccYear,
                start: 0,
                limit: 10,
            };
            this.getAccountAgeDataAndRelatedpartListData(obj, str);
        },
        getAccountAgeDataAndRelatedpartListData(obj, str) {
            this.spinning = true;
            obj.sqlId = str === 'accountAge' ? 'DG40016' : obj.sqlId;
            this.$sacpApis
                .generalQuery(JSON.parse(JSON.stringify(obj)))
                .then((res) => {
                    if (!res.success)
                        return this.$message.error(res.resultInfo.statusText);
                    if (res.data && res.data.length)
                        res.data.forEach((item, index) => {
                            XEUtils.set(item, 'key', index);
                        });
                    if (str === 'accountAge') {
                        this.accountAgeTabledataCollection = res.data;
                        this.pagination2.total = res.totalCount;
                    } else {
                        this.relateList = res.data;
                        this.relateListPagination.total = res.totalCount;
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => (this.spinning = false));
        },
        handleReset(str, clearFilters) {
            clearFilters();
            this.searchText = '';
            this.searchedColumn = '';
            this.queryParams.param3 =
                (str && str === 'merchantName') || !str
                    ? null
                    : this.queryParams.param3;
            this.queryParams.param5 =
                (str && str === 'customerUscc') || !str
                    ? null
                    : this.queryParams.param5;
            this.queryParams.param6 =
                (str && str === 'customerName') || !str
                    ? null
                    : this.queryParams.param6;
            this.queryMerchantListData();
        },
        openSearchDialog() {
            this.openSearchDiaplog = true;
            this.matchValue = null;
            Object.keys(this.searchForm).forEach(
                (key) => (this.searchForm[key] = null)
            );
        },
        openUploadDialog() {
            this.visiableUploadDialog = true;
        },
        handleCancelUpload() {
            this.visiableUploadDialog = false;
            this.fileName = null;
            this.uploadFile = null;
        },
        beforeUpload(file) {
            if (!file || !file.name) return;
            let fileType = file.name.split('.');
            if (
                fileType[fileType.length - 1] != 'xlsx' &&
                fileType[fileType.length - 1] != 'xls'
            ) {
                this.fileName = null;
                this.uploadFile = null;
                return this.$message.error('请上传excel文件格式!');
            } else {
                this.fileName = file.name;
                this.uploadFile = file;
            }
        },
        handleConfirmUpload() {
            let formData = new FormData();
            formData.append('file', this.uploadFile);
            this.spinning = true;
            this.$sacpApis
                .importMerchantRelated(formData)
                .then((res) => {
                    if (res.success) {
                        this.$message.success('上传成功!');
                        this.handleCancelUpload();
                        this.handleQueryContactsCustomer();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.spinning = false;
                });
        },
        actionEmpty() {},
        downLoadImportFile() {
            //  fileId写死,不可更改
            let _fileId = { fileId: 11 };
            this.$sacpApis.downLoad(_fileId, '关联方导入模板.xlsx');
        },
        onSubmitSeachItem(obj) {
            let params = {
                start: obj ? obj.start : 0,
                limit: obj ? obj.limit : 10,
                param3: obj ? obj.param3 : this.searchForm.mergeCustomerName,
                param4: obj ? obj.param4 : this.searchForm.merchantName,
                param5: '', //obj ? obj.param5 : this.searchForm.customerUscc
                param6: obj ? obj.param6 : this.matchValue,
                param8: obj ? obj.param5 : this.searchForm.customerUscc,
            };
            this.getSearchResult(params, obj);
        },
        getSearchResult(params, obj) {
            this.spinning = true;
            let obj1 = {};
            if (params.param6 == '0') {
                obj1.param7 = 1;
            } else if (params.param6) {
                obj1.param6 = 1;
            }
            params.param6 = '';
            let par = {
                ...params,
                param1: session.findUserInfo().curCustomerId,
                param2: session.findUserInfo().curProjectId,
                lockYyyy: record.mergeCurAccYear,
                sqlId: 'DG40007',
                ...obj1,
            };

            this.$sacpApis
                .queryContactsCustomer(par)
                .then((res) => {
                    if (res.success) {
                        if (res.data && res.data.length)
                            res.data.forEach((item, index) => {
                                XEUtils.set(item, 'key', index);
                            });
                        this.dataCollection = res.data;
                        this.pagination1.total = res.totalCount;
                        this.searchFormCache = obj
                            ? this.searchFormCache
                            : JSON.parse(JSON.stringify(this.searchForm));
                        this.searchFormCache.matchValue = obj
                            ? this.searchFormCache.matchValue
                            : this.matchValue;
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    console.log(err, 'err');
                    this.$message.error(err);
                })
                .finally(() => {
                    this.cancelSearch();
                    this.spinning = false;
                    this.searchText = '';
                    // this.searchFormCache=this.searchForm;
                    // this.searchFormCache.matchValue=this.matchValue;
                });
        },
        cancelSearch() {
            this.openSearchDiaplog = false;
            // this.matchValue=null;
            // Object.keys(this.searchForm).forEach(key=>this.searchForm[key]=null);
        },
        matchTableOnSelectChange(selectedRowKeys, selectedRows) {
            this.selectedRowKeys = selectedRowKeys;
            this.selectedMatchRows = selectedRows;
        },
        handleMatch(record) {
            this.currentRow = record;
            this.openHandMatchDiaplog = true;
            this.queryMerchantListData();
        },
        deleteMatch(record) {
            if (!record.customerUscc)
                return this.$message.error('当前客户没有匹配数据!');
            this.spinning = true;
            this.$sacpApis
                .deleteContactsCustomerByHand({
                    param3: record.customerUscc,
                    param4: record.autoId,
                })
                .then((res) => {
                    if (res.success) {
                        this.$message.success('取消成功!');
                        this.handleQueryContactsCustomer();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => {
                    this.spinning = false;
                });
        },
        handleCheckAccountAge(record) {
            this.checkAccountAgeOldItem = record;
            let obj = {
                sqlId: 'DG40006',
                lockCustomerId: record.mergeCustomerId,
                lockProjectId: record.mergeProjectId,
                param3: null,
                param4: null,
                param5: 1,
                start: this.queryParams.start,
                limit: this.queryParams.limit,
            };

            if (!obj.lockCustomerId || !obj.lockProjectId) {
                this.$warning({
                    title: '提示:',
                    content:
                        '关联方客户信息为空，请去合并项目设置关联方清单后再点击同步合并项目的关联方清单按钮重新同步!',
                });
            } else {
                this.openAccountAgeDialog = true;
                this.getAccountAgeDataAndRelatedpartListData(
                    JSON.parse(JSON.stringify(obj)),
                    'accountAge'
                );
            }
        },
        deleteMerchantRelatedById(record) {
            const parmas = { param3: record.autoId };
            this.$sacpApis
                .deleteMerchantRelatedById(parmas)
                .then((res) => {
                    if (res.success) {
                        this.$message.success('删除成功!');
                        this.handleQueryContactsCustomer();
                        // this.cancelMatch();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.spinning = false;
                });
        },
        onSubmitMatchItem() {
            if (!this.selectedRowKeys || this.selectedRowKeys.length == 0)
                return this.$message.error('请选择需要匹配的数据');
            // param3 统一信用代码
            // param4 mergeCustomerId
            // param5:mergeProjectId
            this.spinning = true;
            let customerUsccStr;
            this.selectedMatchRows.map(
                (item) => (customerUsccStr += `,${item.customerUscc}`)
            );
            customerUsccStr = customerUsccStr
                .split(',')
                .filter((item) => item != 'undefined')
                .join();
            let parmas = {
                param3: customerUsccStr,
                param4: this.currentRow.mergeCustomerId,
                param5: this.currentRow.mergeProjectId,
                param6: this.currentRow.autoId,
            };
            this.openHandMatchDiaplog = false;
            this.$sacpApis
                .matchContactsCustomerByHand(parmas)
                .then((res) => {
                    if (res.success) {
                        this.$message.success('匹配成功!');
                        this.handleQueryContactsCustomer();
                        this.cancelMatch();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.spinning = false;
                });
        },
        cancelMatch() {
            this.openHandMatchDiaplog = false;
            this.selectedMatchRows = [];
            this.selectedRowKeys = [];
        },
        queryMerchantListData() {
            this.spinning = true;
            this.queryParams.sqlId = 'DG40004';
            this.queryParams.param4 = null;
            this.queryParams.lockCustomerId =
                session.findUserInfo().curCustomerId;
            this.queryParams.lockProjectId =
                session.findUserInfo().curProjectId;
            this.$sacpApis
                .findMerchantList(this.queryParams)
                .then((res) => {
                    if (res.success) {
                        if (res.data && res.data.length)
                            res.data.forEach((item, index) => {
                                XEUtils.set(item, 'key', index);
                            });
                        this.matchTabledataCollection = res.data;
                        this.pagination.total = res.totalCount;
                        if (this.queryParams.isChange)
                            this.pagination.current = 1; //分页回到第一页
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.searchText = '';
                    this.searchedColumn = '';
                    this.spinning = false;
                });
        },
        relationNumber() {
            let params = {
                param1: session.findUserInfo().curCustomerId,
                param2: session.findUserInfo().curProjectId,
                lockYyyy: session.findUserInfo().curAccYear,
                sqlId: 'DG40046',
            };
            this.$sacpApis
                .findMerchantList(params)
                .then((res) => {
                    if (res.success) {
                        if (res.data && res.data.length) {
                            this.relatioNum = res.data[0].relatedCount;
                        }
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.spinning = false;
                    this.$store.commit('refreshRelatedPartMatch', false);
                });
        },
        handleQueryContactsCustomer(obj) {
            let params = obj
                ? obj
                : {
                      start: 0,
                      limit: 10,
                  };
            let par = {
                ...params,
                param1: session.findUserInfo().curCustomerId,
                param2: session.findUserInfo().curProjectId,
                lockYyyy: session.findUserInfo().curAccYear,
                sqlId: 'DG40007',
            };
            this.spinning = true;
            this.$sacpApis
                .queryContactsCustomer(par)
                .then((res) => {
                    if (res.success) {
                        if (res.data && res.data.length)
                            res.data.forEach((item, index) => {
                                XEUtils.set(item, 'key', index);
                            });
                        this.dataCollection = res.data;
                        this.pagination1.total = res.totalCount;
                        this.relationNumber();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.spinning = false;
                    this.$store.commit('refreshRelatedPartMatch', false);
                });
        },
        manualRefesh() {
            this.handleQueryContactsCustomer();
        },
    },
    created() {
        this.handleQueryContactsCustomer();
    },
};
</script>
<style>
.resize-table-th {
    position: relative;
}
.table-draggable-handle {
    position: absolute;
    transform: none !important;
    bottom: 0;
    height: 100% !important;
    left: auto !important;
    right: -5px;
    cursor: col-resize;
    touch-action: none;
    border: none;
}
</style>


